import React from 'react';
import {Pie} from 'react-chartjs-2';

function PieGraph(props) {
    const data = {
        labels: [
          'Deaths',
          'Recovered',
          'Active'
        ],
        datasets: [{
          label: 'COVID-19 Infographics',
          data: [props.deaths, props.recovered, props.active],
          backgroundColor: [
            'red',
            'green',
            'yellow',
          ],
          hoverOffset: 4,
          maintainAspectRatio:false,
          
        }]
      };

      return  <div>
          <Pie data={data}
              options={{
                plugins:{title:{
                  display:true,
                  text:'COVID-19 DATA',
                  fontSize:20
                }
            },
                legend:{
                  display:true,
                  position:'right'
                },
                height:400,
                
              }} 
              />
            </div>
}

export default PieGraph