import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import LineGraph from '../Charts/Line';
import PieGraph from '../Charts/Pie';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

export default function FullWidthGrid(props) {
  const classes = useStyles();
  const all = [
    {
        label: "Confirmed cases",
        data: props.confirmed,
        backgroundColor:'#f5ec47'

    },
    {
        label: "Deaths",
        data: props.deaths,
        backgroundColor:'#cf0c0c'
    },
    {
        label: "Recovered",
        data: props.recovered,
        backgroundColor:'#30eb1e',
    }
];
const new_cases = [{
    label: "New cases",
    data: props.new_cases,
    borderColor:'#ffa500'
}];
const deaths = [{
    label: "Deaths",
    data: props.deaths,
    backgroundColor:'#cf0c0c'
}];
const recovered = [{
    label: "Recovered",
    data: props.recovered,
    backgroundColor:'#30eb1e',
}];

  return (
    <div style={{ padding: 15 }} className={classes.root}>
      <Grid container spacing={3}>
          
      <Grid item xs={12} sm={6}>
          <Paper className={classes.paper}><h1>Total Cases</h1>
          <h3>Confirmed - {(props.active_t+props.deaths_t+props.recovered_t)/1000000} million</h3>
          <h3>New Cases - {props.today/100000} lakh</h3>
  <h3>Active - {props.active_t/100000} lakh</h3>
  <h3>Deaths - {props.deaths_t/100000} lakh</h3>
  <h3>Recovered - {props.recovered_t/1000000} million</h3>

          
          </Paper>
        </Grid>

        <Grid item xs={12} sm={6}>
          <Paper className={classes.paper}><PieGraph  recovered={props.recovered_t} deaths={props.deaths_t} active={props.active_t} /></Paper>
        </Grid>

        <Grid item xs={12}>
          <Paper className={classes.paper}><LineGraph date={props.date} data = {all} /></Paper>
        </Grid>
        <Grid item xs={12}>
          <Paper className={classes.paper}><LineGraph date={props.date}  data={new_cases}  /></Paper>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Paper className={classes.paper}><LineGraph date={props.date}  data={recovered}  /></Paper>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Paper className={classes.paper}><LineGraph date={props.date}  data={deaths}  /></Paper>
        </Grid>
        
      </Grid>
    </div>
  );
}
