import React from 'react';
import Skeleton from '@material-ui/lab/Skeleton';

import FullWidthGrid from './Layout/layout'
  

class Board extends React.Component{
    state = {
        country:{},
        loading : true,
        search:'India',
        graph :{}
         
     };


    componentDidMount() {

        fetch('https://pomber.github.io/covid19/timeseries.json').then((res)=>res.json()).then((x)=>
           { this.setState({country:x,loading:false})}
        );
     
     
      }
    diff(ary) {
        var newA = [];
        for (var i = 1; i < ary.length; i++)  newA.push(ary[i] - ary[i - 1])
        return newA;
    }
     

    render(){
        
       
        if(this.state.loading){
            return <div style={{padding:'5vw'}}><Skeleton variant="text" width={'90vw'} height={'30vh'}  />
            <Skeleton variant="text" width={'90vw'} height={'30vh'}  />
            <Skeleton variant="text" width={'90vw'} height={'20vh'}  /></div>
        }
        else{
            if(typeof this.state.country[this.state.search] !== "undefined"){
            
                const India = this.state.country[this.state.search].slice();
                const date = India.map((d)=>d.date);
                const confirmed = India.map((d)=>d.confirmed);
                const deaths = India.map((d)=>d.deaths);
                const recovered = India.map((d)=>d.recovered);
                const new_cases = this.diff(confirmed);
            
              const len = deaths.length;
             
              console.log(new_cases);
              return <div> 
            <div>
                
                
            <FullWidthGrid date={date} recovered={recovered} deaths={deaths} confirmed={confirmed} recovered_t={recovered[len-1]} deaths_t={deaths[len-1]} active_t={confirmed[len-1]-deaths[len-1]-recovered[len-1]} today= {confirmed[len-1]-confirmed[len-2]} new_cases = {new_cases}/>
              </div>
            </div>
            }
        
    }
}
}

export default Board